}
.navbar {
  height: 100%;
}
.nav-link {
  text-transform: uppercase;
  font-size: 12px !important;
}
.nav-link:hover {
  color: rgb(255, 145, 0) !important;
}
.navbar-collapse {
  -webkit-flex-grow: 0.73 !important;
          flex-grow: 0.73 !important;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
  /* padding-bottom: 10px; */
