@font-face {
  src: url(./Content/imgs/HIGHSPEED.TTF);
  font-family: speed;
}
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");

.dotted-logo {
  font-family: speed;
  color: whitesmoke;
  font-size: 1em;
}
.ly {
  font-size: 2em;
}

.header {
  width: 100%;
  height: 100px !important;
}
.dlogo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Hitems {
  color: white;
}
.tophead {
  background: white !important;
}
.between {
  background: rgba(255, 255, 255, 0.418) !important;
}
.navbar {
  height: 100%;
}
.nav-link {
  text-transform: uppercase;
  font-size: 12px !important;
}
.nav-link:hover {
  color: rgb(255, 145, 0) !important;
}
.navbar-collapse {
  flex-grow: 0.73 !important;
  justify-content: space-between;
  margin-top: 10px;
  /* padding-bottom: 10px; */
  z-index: 1;
  background-color: white;
}
.navbar-brand {
  flex-grow: 0 !important;
}
.navbar-nav {
  flex-grow: 0.5 !important;
  justify-content: space-around !important;
}

.shadow::before {
  content: "";
  position: fixed;
  top: -10px;
  left: 0;
  width: 100%;
  height: 10px;
  box-shadow: 0px 0px 50px 50px black;
}
